@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;500;600&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body,
html,
code {
  font-family: "Mulish", sans-serif;
  background-color: #f2f2f2;
  height: 200vh;
  width: 100%;
  line-height: 1.4;
}

h1,
h2,
h3,
p {
  margin: 0;
}

.home__container {
  height: 100%;
  width: 100%;
}

.scrolltotop__container {
  width: 50px;
  height: 50px;
  align-items: center;
  justify-content: center;
  flex-flow: column nowrap;
  position: fixed;
  bottom: 0.5rem;
  right: 0.5rem;
  background-color: #262223;
  border-radius: 50%;
}

.scrolltotop__btn {
  width: 100%;
  height: 100%;
  color: #f2f2f2;
  cursor: pointer;
  -webkit-animation: fadeIn 0.3s;
          animation: fadeIn 0.3s;
}

@-webkit-keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.7;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 0.7;
  }
}

.landinghero__container {
  height: 100vh;
  width: 100%;
  background: #f2f2f2;
}

.landinghero__wrapper {
  width: 85%;
  height: 100%;
  max-width: 65em;
  margin: 0 auto;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column nowrap;
}

.landinghero__primarytitle {
  font-size: 4rem;
  font-weight: 600;
  color: #262223;
}

.landinghero__subtitle {
  font-size: 1.5rem;
  font-weight: 600;
  color: #262223;
}

.landinghero__primarytitle-0 {
  font-size: 2rem;
  font-weight: 600;
  color: #262223;
}

.landinghero__interest {
  font-size: 3rem;
  font-weight: 600;
  color: #f2811d;
  transition: 0.4s;
  display: inline-block;
}

@supports (background-blend-mode: multiply) {
  .landinghero__container {
    background-image: var(--backgroundinterestImage),
      radial-gradient(#262223, #262223, #262223);
    background-blend-mode: multiply;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center center;
  }

  .landinghero__primarytitle {
    color: #f2f2f2;
  }

  .landinghero__subtitle {
    color: #f2f2f2;
  }

  .landinghero__primarytitle-0 {
    color: #f2f2f2;
  }
}

.scrolldown__container {
  position: absolute;
  bottom: 0;
  right: 0;
  width: 200px;
  height: 4em;
}

.scrolldown__mouse {
  width: 26px;
  height: 40px;
  position: relative;
  top: 10px;
  right: 0;
  border-radius: 18px;
  border: 2px solid #f2f2f2;
}

.scrolldown__mousebutton {
  background-color: #f2f2f2;
  width: 4px;
  height: 10px;
  border-radius: 2px;
  position: absolute;
  top: 10px;
  left: 50%;
  margin: 0 0 0 -2px;
  -webkit-animation: mouse-scroll 1s infinite alternate;
          animation: mouse-scroll 1s infinite alternate;
}

.scrolldown__infotext {
  margin-top: -30px;
  margin-left: 40px;
  color: #f2f2f2;
  font-size: 1rem;
  line-height: 1em;
}

@-webkit-keyframes mouse-scroll {
  to {
    transform: translate(0, 6px) scale(1, 0.8);
  }
}

@keyframes mouse-scroll {
  to {
    transform: translate(0, 6px) scale(1, 0.8);
  }
}

@media screen and (max-width: 812px) {
  .scrolldown__container {
    display: none;
  }
}


.comingsoon__highlightcolor {
  color: #f2811d;
}

.comingsoon__container {
  text-align: center;
  padding: 15em 0;
  height: 100vh;
  width: 100%;
  background-color: #f2f2f2;
}

.comingsoon__wrapper {
  width: 85%;
  max-width: 65em;
  margin: 0 auto;
}

.comingsoon__wrapper h1 {
  color: #262223;
  font-weight: 500;
  font-size: 2rem;
}

.comingsoon__wrapper img {
  width: 15rem;
  height: 15rem;
}

:root {
  --duration: 5000ms;
}

.loader__container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-flow: column nowrap;
  background-color: #f2f2f2;
  width: 100%;
  height: 100vh;
  overflow: hidden;
}

.loader__title {
  color: #262223;
  font-style: 2rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  font-weight: 600;
  -webkit-animation: colorflow 1.3s infinite ease;
          animation: colorflow 1.3s infinite ease;
}

@-webkit-keyframes colorflow {
  0% {
    color: #262223;
  }
  100% {
    color: #f2811d;
  }
}

@keyframes colorflow {
  0% {
    color: #262223;
  }
  100% {
    color: #f2811d;
  }
}

.loader__cubegrid {
  width: 40px;
  height: 40px;
}

.loader__cube {
  width: 33%;
  height: 33%;
  background-color: #262223;
  float: left;
  -webkit-animation: cubegridScaleDelay 1.3s infinite ease-in-out;
          animation: cubegridScaleDelay 1.3s infinite ease-in-out;
}

.loader__cube1 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.loader__cube2 {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

.loader__cube3 {
  -webkit-animation-delay: 0.4s;
          animation-delay: 0.4s;
}

.loader__cube4 {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

.loader__cube5 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

.loader__cube6 {
  -webkit-animation-delay: 0.3s;
          animation-delay: 0.3s;
}

.loader__cube7 {
  -webkit-animation-delay: 0s;
          animation-delay: 0s;
}

.loader__cube8 {
  -webkit-animation-delay: 0.1s;
          animation-delay: 0.1s;
}

.loader__cube9 {
  -webkit-animation-delay: 0.2s;
          animation-delay: 0.2s;
}

@-webkit-keyframes cubegridScaleDelay {
  0%,
  70%,
  100% {
    transform: scale3d(1, 1, 1);
  }
  35% {
    transform: scale3d(0, 0, 1);
    background-color: #f2811d;
  }
}

@keyframes cubegridScaleDelay {
  0%,
  70%,
  100% {
    transform: scale3d(1, 1, 1);
  }
  35% {
    transform: scale3d(0, 0, 1);
    background-color: #f2811d;
  }
}

.navbar__container {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  padding: 40px 100px;
  justify-content: space-between;
  align-items: center;
  transition: 0.6s;
  background: #262223;
  opacity: 0.5;
}

.navbar__logo {
  position: relative;
  text-transform: capitalize;
  font-weight: 600;
  font-size: 2rem;
  padding: 0 10px;
  color: #f2f2f2;
  text-decoration: none;
  letter-spacing: 2px;
  transition: 0.6s;
}

.navbar__menu {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
}

.navbar__menuitem {
  position: relative;
  color: #f2f2f2;
  text-transform: uppercase;
  text-decoration: none;
  padding: 0 10px;
  outline: none;
  font-weight: 600;
  transition: 0.6s;
  letter-spacing: 2px;
  margin: 0 15px;
}

.navbar__menuitem--active {
  color: #f2811d;
  border-bottom: 1px solid #f2811d;
}

.navbar__showmenubtn {
  padding: 0 10px;
}

.navbar__showmenubtn,
.navbar__closemenubtn {
  transition: 0.4s;
  color: #f2f2f2;
  font-size: 2rem;
  cursor: pointer;
  display: none;
}

.navbar__menuitem:hover,
.navbar__showmenubtn:hover,
.navbar__closemenubtn:hover {
  color: #f2811d;
}

#navbar-chk {
  position: absolute;
  visibility: hidden;
  z-index: -1111;
}

.navbar__container--sticky {
  padding: 5px 100px;
  background: #262223;
  opacity: 1;
  z-index: 100;
}

@media screen and (max-width: 812px) {
  .navbar__container {
    padding: 40px 10px;
  }
  .navbar__logo {
    font-size: 1.2rem;
  }

  .navbar__showmenubtn,
  .navbar__closemenubtn {
    display: block;
    transition: 0.6s;
  }

  .navbar__menu {
    position: fixed;
    width: 100%;
    height: 100vh;
    background-color: #262223;
    right: -100%;
    top: 0;
    text-align: center;
    padding: 100px 0;
    line-height: normal;
    transition: 0.7s;
    opacity: 0.986;
    flex-flow: column nowrap;
  }

  .navbar__menuitem {
    display: block;
    color: #f2f2f2;
    padding: 20px;
    transition: 0.6s;
  }

  .navbar__menuitem--active {
    color: #f2811d;
    border: none;
  }

  .navbar__closemenubtn {
    position: absolute;
    top: 30px;
    right: 30px;
  }

  .navbar__menu-open {
    right: 0;
  }

  .navbar__showmenubtn-display {
    display: none;
  }
}

